<template>
	<div>
		<loading :active.sync="isLoading"></loading>
		<div class="row mt-4">
			<div class="col-md-4 mb-4" v-for="(product, key) in products" :key="key">
			<div class="card border-0 shadow-sm">
				<div style="height: 150px; 
					background-size: cover; background-position: center"
					:style="bgcss(product.imageUrl)">
				</div>
				<div class="card-body">
					<span class="badge badge-secondary float-right ml-2">{{ product.category }}</span>
					<h5 class="card-title">
						<a href="#" class="text-dark">{{ product.title }}</a>
					</h5>
					<p class="card-text">{{ product.content }}</p>
					<div class="d-flex justify-content-between align-products-baseline">
						<!-- <div class="h5">2,800 元</div> -->
						<div class="h6" v-if="product.price">原價{{ product.origin_price }}元</div>
						<div class="h6" v-if="!product.price">原價{{ product.origin_price }}元</div>
						<div class="h5">現在只要{{ product.price }}元！</div>
					</div>
				</div>
				<div class="card-footer d-flex">
					<button type="button" 
						class="btn btn-outline-secondary btn-sm"
						@click.prevent="getProduct(product.id)">
						<i class="fas fa-spinner fa-spin" v-if="status.productLoading === product.id"></i>
						查看更多
					</button>
					<button type="button" 
						class="btn btn-outline-danger btn-sm ml-auto"
						@click="addtoCart(product.id)">
						<i class="fas fa-spinner fa-spin" v-if="status.productLoading === product.id"></i>
						加到購物車
					</button>
				</div>
			</div>
		</div>
		</div>

		<div class="modal fade" id="productModal" tabindex="-1" role="dialog"
			aria-labelledby="exampleModalLabel" aria-hidden="true">
			<div class="modal-dialog" role="document">
				<div class="modal-content">
				<div class="modal-header">
					<h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
					<button type="button" class="close" data-dismiss="modal" aria-label="Close">
					<span aria-hidden="true">&times;</span>
					</button>
				</div>
				<div class="modal-body">
					<img :src="product.image" class="img-fluid" alt="">
					<blockquote class="blockquote mt-3">
					<p class="mb-0">{{ product.content }}</p>
					<footer class="blockquote-footer text-right">{{ product.description }}</footer>
					</blockquote>
					<div class="d-flex justify-content-between align-items-baseline">
					<div class="h4" v-if="!product.price">{{ product.origin_price }} 元</div>
					<del class="h6" v-if="product.price">原價 {{ product.origin_price }} 元</del>
					<div class="h4" v-if="product.price">現在只要 {{ product.price }} 元</div>
					</div>
					<select name="" class="form-control mt-3" v-model="product.num">
					<option :value="num" v-for="num in 10" :key="num">
						選購 {{num}} {{product.unit}}
					</option>
					</select>
				</div>
					<div class="modal-footer">
						<div class="text-muted text-nowrap mr-3">
						小計 <strong>{{ product.num * product.price }}</strong> 元
						</div>
						<button type="button" class="btn btn-primary"
						@click="addtoCart(product.id, product.num)">
						<i class="fas fa-spinner fa-spin" v-if="product.id === status.productLoading"></i>
						加到購物車
						</button>
					</div>
				</div>		
			</div>	
		</div>

		<nav aria-label="Page navigation example">
			<Pagination 
				:paginationData="pagination" 
				v-on:emitpage="getProducts">
			</Pagination>
    </nav>

		<div class="mx-auto">
			<table class="table">
			<thead>
				<th></th>
				<th>品名</th>
				<th>數量</th>
				<th>單價</th>
			</thead>
			<tbody>
				<tr v-for="item in cart.carts" :key="item">
					<td class="align-middle">
							<button type="button" 
								class="btn btn-outline-danger btn-sm"
								@click="deleteCart(item.id)">
								<i class="far fa-trash-alt"></i>
							</button>
					</td>
					<td class="align-middle">
						{{ item.product.title }}
						<div class="text-success" v-if="item.coupon">
							已套用優惠券
						</div>
					</td>
					<td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
					<td class="align-middle text-right">{{ parseInt(item.final_total) }}</td>
				</tr>
			</tbody>
			<tfoot> 
				<tr>
					<td colspan="3" class="text-right">總計</td>
					<td class="text-right">{{ cart.total }}</td>
				</tr>
				<tr>
					<td colspan="3" class="text-right text-success">折扣價</td>
					<td class="text-right text-success">{{ parseInt(cart.final_total)}}</td>
				</tr>
			</tfoot>
			</table>

			<hr>

			<div class="input-group mb-3 input-group-sm">
				<input type="text" 
					class="form-control" 
					placeholder="請輸入優惠碼"
					v-model="couponCode.code">
				<div class="input-group-append">
					<button 
						class="btn btn-outline-secondary" 
						type="button"
						@click="getCoupon()">
						套用優惠碼
					</button>
				</div>
			</div>
		</div>

		<div class="my-5 row justify-content-center">
			<form class="col-md-6" @submit.prevent="submitOrder">
				<div class="form-group">
					<label for="useremail">Email</label>
					<ValidationProvider name="email" rules="required|email" v-slot="{ errors }">
						<input type="email" class="form-control" name="email" id="useremail"
							v-model="form.user.email" placeholder="請輸入 Email" required>
								<span class="text-danger">{{ errors[0] }}</span>
					</ValidationProvider>
				</div>
			
				<div class="form-group">
					<label for="username">收件人姓名</label>
					<ValidationProvider name="姓名" rules="required" v-slot="{ errors }">
						<input type="text" class="form-control" name="name" id="username"
							v-model="form.user.name" placeholder="輸入姓名">
						<span class="text-danger">{{ errors[0] }}</span>
					</ValidationProvider>
				</div>
			
				<div class="form-group">
					<label for="usertel">收件人電話</label>
					<ValidationProvider name="電話" rules="required" v-slot="{ errors }">
						<input type="tel" class="form-control" id="usertel" v-model="form.user.tel" placeholder="請輸入電話">
						<span class="text-danger">{{ errors[0] }}</span>
					</ValidationProvider>
				</div>
			
				<div class="form-group">
					<label for="useraddress">收件人地址</label>
					<input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
						placeholder="請輸入地址">
					<span class="text-danger">地址欄位不得留空</span>
				</div>
			
				<div class="form-group">
					<label for="comment">留言</label>
					<textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
				</div>
				<div class="text-right">
					<button class="btn btn-danger">送出訂單</button>
				</div>
			</form>
		</div>
	</div>
</template>

<script>

import $ from 'jquery';
import { extend } from 'vee-validate';
import Pagination from './pagination';


// extend('form',  => { // 這邊可以改寫我們 email 告知使用者是必填欄位的提示文字
//   if( == ''){
//     return 'The {_field_} field must be a positive number'
//   }
// })

export default {
	components: {
		Pagination,
	},

	data(){
		return{
			products: [],
			isLoading: false,
			status: {
				productLoading: ''
			},
			product: {},
			cart: {},
			couponCode: {
				code: ''
			},

			pagination: {},
			
			form: {
				"user": {
				"name": "",
				"email": "",
				"tel": "",
				"address": ""
				},
				"message": ""
			}
		}
	},

	methods:{
		getProducts(page = 1){
			const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/products?page=${page}`
			const vm = this
			vm.isLoading = true
			this.$http.get(api).then((response)=>{
				vm.isLoading = false
				vm.products = response.data.products
				vm.pagination = response.data.pagination
			})
		},

		getProduct(id){
			const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/product/${id}`
			const vm = this
			vm.status.productLoading = id
			this.$http.get(api).then((response)=>{
				vm.product = response.data.product
				vm.pagination = response.data.pagination
				vm.status.productLoading = ''
				$("#productModal").modal("show")
			})
		},

		addtoCart(id, qty = 1){
			const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`
			const vm = this
			const cart = {
				product_id: id,
				qty
			}
			vm.status.productLoading = id
			this.$http.post(api, {data : cart}).then((response)=>{
				// console.log(response) 
				vm.status.productLoading = ''
				vm.getCart()
			})
		},

		deleteCart(id){
			const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`
			const vm = this
			vm.isLoading = true
			this.$http.delete(api).then((response)=>{
				// console.log(response) 
				if(response.data.success){
					vm.getCart()
					vm.$bus.$emit('message:push', response.data.message, 'success')
					vm.isLoading = false
				}else{
					vm.$bus.$emit('message:push', response.data.message, 'danger')
				}
			})
		},

		getCart(){
			const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`
			const vm = this
			vm.isLoading = true
			this.$http.get(api).then((response)=>{
				console.log(response) 
				vm.cart = response.data.data
				vm.isLoading = false
			})
		},

		getCoupon(){
			const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`
			const vm = this
			this.$http.post(api, {data : vm.couponCode}).then((response)=>{
				if(response.data.success){
					console.log(response) 
					vm.getCart()
					vm.$bus.$emit('message:push', response.data.message, 'success')
				}else{
					vm.$bus.$emit('message:push', response.data.message, 'danger')					
				}
			})
		},

		submitOrder(){
			const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`
			const vm = this
				this.$http.post(api, {data : vm.form}).then((response)=>{
					console.log(response) 
					if(response.data.success){
						console.log(response)
						vm.getCart()
						vm.$router.push(`/_admin/checkout/${response.data.orderId}`)
						vm.$bus.$emit('message:push', response.data.message, 'success')
					}else{
						vm.$bus.$emit('message:push', response.data.message, 'danger')					
				}
			})	
		},

		bgcss(img){
			return {
				'background-image' : `url(${img})`
			}
		}
	},

	created(){
		this.getProducts()
		this.getCart()
	}
}	

</script>


